import { createRouter, createWebHistory } from 'vue-router'
import HomeView from '../views/HomeView.vue'

const routes = [
  {
    path: '/',
    name: 'home',
    component: HomeView
  },
  {
    path: '/MineSweeper',
    name: 'MineSweeper',
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () => import(/* webpackChunkName: "about" */ '../views/MineSweeper.vue')
  },
  {
    path: '/Snake',
    name: 'Snake',
    component: () => import('../views/SnakeView.vue')
  },
  {
     path: '/WordSearch',
     name: 'WordSearch',
     component: () => import('../views/WordSearchView.vue')
  },
  {
    path: '/Stacker',
    name: 'Stacker',
    component: () => import("../views/StackerView.vue")
  },
  {
    path: '/Highscores',
    name: 'Highscores',
    component: () => import ('../views/HighScoresView.vue')
  },
  {
    path: '/ScoreVisualization',
    name: 'ScoreVisualization',
    component: () => import ('../views/ScoreVisualization.vue')
  },
]

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes
})

export default router
